.blog-contents {
        margin-top: 24.84px;
        display: flex;
        flex-direction: column;
        align-items: center;
		// .blog-contents__sphere

		&__sphere {
            background: #F3F5F6;
            margin-top: 16px;
            box-shadow: 0px 1px 2px rgba(105, 81, 255, 0.05);
            border-radius: 36px;
            padding: 5px 12px 5px 12px;

            font-family: 'Red Hat Display';
            font-style: normal;
            font-weight: 500;
            font-size: 12px;
            line-height: 18px;
            /* identical to box height, or 150% */

            text-align: center;

            /* Primary color */

            color: #FFC93E;
		}

		// .blog-contents__autor

		&__autor {
            margin-top: 16px;
            font-family: 'Inter';
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 24px;
            /* identical to box height, or 150% */

            text-align: center;

            /* White color */

            color: #FFFFFF;
		}

		// .blog-contents__subtitle

		&__subtitle {
            font-family: 'Inter';
            font-style: normal;
            font-weight: 700;
            font-size: 20px;
            line-height: 38px;
            /* or 190% */

            text-align: center;

            /* White color */

            color: #FFFFFF;
		}
}

