.section-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
		// .section-header__title

		&__title {
            font-family: 'Inter';
            font-style: normal;
            font-weight: 700;
            font-size: 24px;
            line-height: 29px;
            text-transform: capitalize;

            /* White color */

            color: #FFFFFF;

		}

		// .section-header__link

		&__link {
            font-family: 'Inter';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;

            /* Primary color */

            color: #FFC93E;
		}
}
