.header-conteiner {
    padding: 2.33333333333rem 1.25rem 0 1.25rem ;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.main-header-navigation {
    display: flex;
    justify-content: flex-end;
    width: 100%;
    @media screen and (max-width: 680px) {
        position: absolute;
        height: 0;
        top: 70px;
        right: 0;
        overflow: hidden;
        flex-direction: column;
        align-items: flex-end;
        width: 100%;
        background-color: $main-blue;
        transition: height 0.5s linear;
    }
    &.active {
        height: 250px;
        transition: height 0.5s linear;
    }

        &__link {
    position: relative;
    font-size: 20px;
    text-transform: lowercase;
    font-weight: 800;
    margin-left: 5%;
    color: #fff;
    &::after {
        content: "";
        display: inline-block;
        position: absolute;
        right: 0;
        top: 30px;
        height: 2px;
        width: 0;
        background-color: rgb(97, 217, 251);
        transition: width 0.3s ease-out;
    }
    &:hover:after {
        width: 100%;
        color: rgb(97, 217, 251);
        @media screen and (max-width: 680px) {
            width: 0;
        }
    }
    &:hover {
        color: rgb(97, 217, 251);
    }
    @media screen and (max-width: 680px) {
        margin: 20px 15px;
    }
}
}
    

