.main-footer {
	background-color: $main-grey;
    padding-top: 40px;
    padding-bottom: 29px;
}

.main-footer-form {
		background-color: $main-yellow;
        padding: 23.622047244% 14.678899082%;
        border-radius: 20px;
		display: flex;
		flex-direction: column;

		// .main-footer-form__label

		&__label {
			
			font-family: 'Inter';
            font-style: normal;
            font-weight: 600;
            font-size: 20px;
            line-height: 150%;
            color: #0A142F;
            text-align: start;
		}

		// .main-footer-form__input

		&__input {
			outline: none;
			border: none;
			border-bottom: 1px solid #F3F5F6;
			background-color: $main-yellow;
			margin-top: 15px;
		}
		&__input::placeholder{
			color: #0A142F;
		}

}
.main-footer-container-content {
			display: grid;
			gap: 16px;
		// .main-footer-container-content__title
			&__title {
				font-family: 'Inter';
				font-style: normal;
				font-weight: 600;
				font-size: 18px;
				line-height: 18px;
				/* identical to box height, or 100% */
				
				text-align: center;
				
				/* White color */
				
				color: #FFFFFF;
			}
		

		// .main-footer-container-content__subtitle

		&__subtitle {
			font-family: 'Inter';
			font-style: normal;
			font-weight: 400;
			font-size: 16px;
			line-height: 30px;
			/* or 188% */

			text-align: center;

			/* White color 2 */

			color: #E0E5F3;
		}
}
.main-footer-container-content-logotips {
			display: flex;
			justify-content: space-between;
			width: 66.141732283%;
			margin: 0 auto;

		// .main-footer-container-content-logotips__logo

		&__logo {
		}
}
.main-footer-container{
	display: grid;
	gap: 40px;
	padding-top: 40px;
}

.main-footer-container-content--font{
	line-height: 50px;
}