.wrapper-bagraund {
    background: $main-blue;
    position: relative;

    &__grid-imeg {
    position: absolute;
    left: 0;
    top: 0;
    }
    &__filter-topleft{
        position: absolute;
        width: 222px;
        height: 222px;
        left: -17px;
        
        background: #7C3EFF;
        filter: blur(250px);
        transform: matrix(1, 0, 0, -1, 0, 0);
    }
    &__filter-topright{
        position: absolute;
        width: 141px;
        height: 141px;
        right: 0;
        z-index: 1;
        
        background: #BE229C;
        filter: blur(250px);
        transform: matrix(1, 0, 0, -1, 0, 0);
    }

    &__filter-centerright{
        position: absolute;
        width: 174px;
        height: 174px;
        right: 0;
        top: 200px;

        background: #FFC93E;
        filter: blur(250px);
        transform: matrix(1, 0, 0, -1, 0, 0);
    }

    &__filter-centerleft{
        position: absolute;
        width: 175px;
        height: 175px;
        left: -41px;
        top: 80%;
        
        background: #2286BE;
        filter: blur(250px);
        transform: matrix(1, 0, 0, -1, 0, 0);
    }

    &__ornament-centerright{
        position: absolute;
        right: -15px;
        top: 80%;
    }
}
