.preview {
padding-top: 2.66666666667rem;
    // .preview__ornament-line

    &__ornament-line {
        position: absolute;
    }

    // .preview__ornament-star-topright

    &__ornament-star-topright {
        position: absolute;
        top: -30px;
        right: 20px;
    }

    // .preview__ornament-star-center

    &__ornament-star-center {
        position: absolute;
        right: 31.4666666667%;
        top: 32%;
    }

    // .preview__ornament-star-bootonright

    &__ornament-star-bootonright {
        position: absolute;
        top: 62%;
        right: 4%;
        z-index: 2;
    }

    // .preview__portrets-job

    // .preview__portets-comand
}

.position-container {
    position: relative;
}
.preview-content {

    // .preview-content__tabls
    &__tabls {
        position: absolute;
        top: 0;
        right: 70%;
    }

    // .preview-content__titles

    &__titles {
position: relative;
z-index: 2;
    }
    // .preview-content__title

    &__title {
        @include font("Inter", 700, 5.83333333333rem, 85px, #ffffff);
        margin-left: 25%;
    }

    // .preview-content__subtitle

    &__subtitle {
        @include font("Inter", 700, 70px, 85px, #ffffff);
    }

    // .preview-content__content

    &__content {
        @include font("Inter", 400, 18px, 32px, #ffffff);
        text-align: start;
        margin-top: 16px;
    }

    // .preview-content__button

    &__button {
        position: relative;
        z-index: 2;
        margin-top: 24px; 
        padding: 16px 24px;
        background: #FFC93E;
        border: none;
        border-radius: 30px;    
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 22px;
    }
}
.preview-content__button-arrows-img{
    padding-left: 8px;
}

.preview-portrets-job {
    padding-top: 40px;
    position: relative;
    // .preview-portrets-job__img

    &__img {
        width: 100vw;
    }

    // .preview-portrets-job__playvideo

    &__playvideo {
            position: absolute;
            right: 12.23%;
            bottom: -37.56px;
    }
}
.preview-portrets-comand {
    margin-top: 65.56px;
    position: relative;
    // .preview-portrets-comand__ornament

    &__ornament {
        position: absolute;
        top: 13.04%;
        left: 118px;
    }



    // .preview-portets-comand__img

    &__img {
    }
}